                            <div class="orders-table-tr-flex">
                                <div class="orders-table-td title"
                                     data-title="Кредит"
                                     @click="order.status != 1 ? $router.push(`/orders/${order.id}`) : false">{{order.name}}</div>
                                <div class="orders-table-td" data-title="Сумма">
                                    <div class="orders-price">{{$options.filters.numberFilter(order.sum)}} <span class="icon-ruble-light"></span></div>
                                </div>
                                <div class="orders-table-td" data-title="Ставка">{{order.rate}}</div>
                                <div class="orders-table-td" data-title="Период">{{order.term}}</div>
                                <div class="orders-table-td" data-title="Статус">
                                    <div v-if="visibleStick(order.status)"
