                                    </div>
                                    <div v-else-if="order.status === 1"
                                         class="order-filled-timeline">
                                        <div class="order-filled-timeline-title">Заполнена на {{stepValue(order.step)}}%</div>
                                        <div class="order-filled-timeline-line"><span :style="{width: stepValue(order.step) + '%'}"></span></div>
                                    </div>
                                </div>
                                <div v-if="order.status == 1"
                                     class="orders-table-td submit">
                                    <div class="orders-table-btn">
                                        <router-link class="btn btn-sm btn-border"
                                                     :to="`/create-order/${order.id}`">Продолжить
                                        </router-link>
                                    </div>
                                </div>
                                <div v-if="order.status == 8"
                                     class="orders-table-td submit">
                                    <div class="orders-table-btn">
                                        <button class="btn btn-sm btn-no-wide"
                                                @click="$router.push(`/load-documents/${order.id}`)">Загрузить документы</button>
                                    </div>
                                </div>
                                <div v-if="order.status == 9"
                                     class="orders-table-td submit">
                                    <div class="orders-table-btn">
                                        <a href="#"
                                           @click.prevent="orderReject(order.id)"
                                           class="btn btn-sm btn-secondary btn-no-wide"
                                           :class="{'loading': rejectLoading}"
                                           :disabled="rejectLoading">Отказаться</a>
                                        <a href="#"
                                           @click.prevent="getCredit(order.id)"
                                           class="btn btn-sm btn-no-wide"
                                           :class="{'loading': signingLoading}"
                                           :disabled="signingLoading">Получить</a>
                                    </div>
                                </div>
                                <div v-if="order.status == 14"
                                     class="orders-table-td submit">
                                    <div class="orders-table-btn">
                                        <button class="btn btn-sm btn-no-wide"
                                                :class="{'loading': signingLoading}"
                                                :disabled="signingLoading"
                                                @click="signingOrder(order.id)">Подписать</button>
                                    </div>
                                </div>
                            </div>

                            <div v-if="false" class="orders-commission">
                                <div class="h3">Для получения кредита необходимо оплатить:</div>
                                <div class="orders-commission-item">
                                    <div class="orders-commission-item-info">
                                        <p>Комиссия за выдачу кредита</p>
                                        <p>Сумма комиссии: <span>26 071,00</span></p>
                                    </div>
                                    <div class="orders-commission-item-requisites">
                                        <a href="#"
                                           class="btn btn-border btn-sm"
                                           @click.prevent="readCommission(order, 'readCommissionRequisites')">Реквизиты для оплаты</a>
                                    </div>
                                    <div class="orders-commission-item-checkbox">
                                        <Tooltip>Для получения кредита, пожалуйста ознакомьтесь с рекзвизитами для оплаты и оплатите необходимую комиссию</Tooltip>
                                        <FormGroupSwitchCheckbox v-model="order.commissionIssue"
                                                                 :disabled="!order.readCommissionRequisites">Я оплатил комиссию</FormGroupSwitchCheckbox>
                                    </div>
                                </div>
                                <div class="orders-commission-item">
                                    <div class="orders-commission-item-info">
                                        <p>Договор страхования жизни</p>
                                        <p>Сумма комиссии: <span>26 071,00</span></p>
                                    </div>
                                    <div class="orders-commission-item-requisites">
                                        <a href="#"
                                           class="btn btn-border btn-sm"
                                           @click.prevent="readCommission(order, 'readCommissionInsurance')">Реквизиты для оплаты</a>
                                    </div>
                                    <div class="orders-commission-item-checkbox">
                                        <Tooltip>Для получения кредита, пожалуйста ознакомьтесь с рекзвизитами для оплаты и оплатите необходимую комиссию</Tooltip>
                                        <FormGroupSwitchCheckbox v-model="order.commissionInsurance"
                                                                 :disabled="!order.readCommissionInsurance">Я оплатил комиссию</FormGroupSwitchCheckbox>
                                    </div>
                                </div>
                            </div>

                            <div v-if="false" class="orders-commission">
                                <div class="h3">Для получения кредита необходимо будет оплатить:</div>
                                <div class="orders-commission-item">
                                    <div class="orders-commission-item-info">
                                        <p>Сумма комиссии за выдачу кредита</p>
                                        <p>Сумма: <span>26 071,00</span></p>
                                    </div>
                                </div>
                                <div class="orders-commission-item">
                                    <div class="orders-commission-item-info">
                                        <p>Страховая премия по ДСЖ</p>
                                        <p>Сумма: <span>26 070,00</span></p>
                                    </div>
                                </div>
                            </div>

                        </div>
                    </template>

                </div>

            </div>

            <div v-if="withBtn"
                 class="new-order-link">
                <a @click.prevent="createNewOrder"
                   href="#"
                   class="btn">Оформить новую заявку</a>
            </div>

            <template v-if="ordersFinished !== undefined">
                <div v-if="ordersFinished.length > 0"
                     class="block">
                    <div class="block-title">
                        <div class="h3">Выданные кредиты</div>
                    </div>
                    <div class="block-content">
                        <LoansIssued :orders="ordersFinished"/>
                    </div>
                </div>
            </template>


            <RejectOrderModals :rejectOrderConfirm="rejectOrderConfirm"
                               :orderId="rejectOrderId"
                               @close-confirm-modal="closeConfirmModal"/>

            <Popup v-if="reservedAccount"
                   ref="reservedAccount"
                   @close-modal="reservedAccount = false">
                <div class="popup-title">
                    <div class="h2">Вам зарезервирован счет №1615262</div>
                </div>
                <div class="popup-body">
                    <p>Денежные средства будут перечислены на зарезервированный счет. Для совершения расходных операций по счету необходимо подписать документы в отеделении банка.</p>
                    <button class="btn btn-lg btn-block"
                            @click="checkBranches">Посмотреть отделения</button>
                </div>
            </Popup>

            <SelectBranch v-if="selectBranch"
                          @close-select-branch="selectBranch = false"
                          @can-next-step="nextOrderStep"
                          @selected-branch="selectedBranch"/>

            <Popup v-if="requisitesModal"
                   ref="requisitesModal"
                   @close-modal="requisitesModal = false">
                <div class="popup-title">
                    <div class="h2">Реквизиты для оплаты комиссии</div>
                </div>
                <div class="popup-body">
                    <ul class="requisites-list">
                        <li>
                            <span>Получатель</span>
                            <span>Общество с ограниченной ответственностью «Весна»</span></li>
                        <li>
                            <span>Адрес</span>
                            <span>123456, г. Москва, ул. Подвойского, д. 14, стр. 7</span></li>
                        <li>
                            <span>ИНН</span>
                            <span>7712345678</span></li>
                        <li>
                            <span>КПП</span>
                            <span>779101001</span></li>
                        <li>
                            <span>Получатель</span>
                            <span>Общество с ограниченной ответственностью «Весна»</span></li>
                        <li>
                            <span>Адрес</span>
                            <span>123456, г. Москва, ул. Подвойского, д. 14, стр. 7</span></li>
                        <li>
                            <span>ИНН</span>
                            <span>7712345678</span></li>
                        <li>
                            <span>КПП</span>
                            <span>779101001</span></li>
                    </ul>
                    <button class="btn btn-lg btn-block close-modal">Закрыть</button>
                </div>
            </Popup>

        </div>

    </div>

</template>

<script>

    import {mapState} from "vuex";
    import SelectBranch from "@/components/Blocks/SelectBranch";
    import RejectOrderModals from "@/components/Blocks/RejectOrderModals";
    import Popup from "@/components/Blocks/Popup";
    import FormGroupSwitchCheckbox from "@/components/Inputs/FormGroupSwitchCheckbox";
    import Tooltip from "@/components/ui-elements/Tooltip";
    import LoansIssued from "@/components/Blocks/LoansIssued"

    export default {
        components: {
            Popup,
            RejectOrderModals,
            SelectBranch,
            FormGroupSwitchCheckbox,
            Tooltip,
            LoansIssued
        },
        props: {
            quantity: {
                type: Number,
                default: null
            },
            withBtn: {
                type: Boolean,
                default: true
            }
        },
        computed: {
            ...mapState({
                mapSettings: state => state.mapApi.mapSettings,
                currentUser: state => state.users.currentUser,
                company: state => state.users.company,
                orders: state => state.orders.orders,
                ordersFinished: state => state.orders.ordersFinished,
            }),
            needDocuments() {
                if ( this.orders !== undefined ) {
                    return this.orders.filter(order => order.status === 8)
                }
                else {
                    return []
                }
            }
        },
        data: () => ({
            loading: true,
            howToGetModal: false,
            selectBranch: false,
            markerSettings: {
                layout: 'default#image',
                imageHref: require('@/assets/images/dist/icons/marker.svg'),
                imageSize: [16, 21],
                imageOffset: [-8, -21],
            },
            activeMarkerSettings: {
                layout: 'default#image',
                imageHref: require('@/assets/images/dist/icons/marker-active.svg'),
                imageSize: [16, 21],
                imageOffset: [-8, -21],
            },
            rejectOrderConfirm: false,
            reservedAccount: false,
            requisitesModal: false,
            rejectOrderId: '',
            signingLoading: false,
            rejectLoading: false
        }),
        methods: {
            orderStatus(id) {
                switch (id) {
                    case 1:
                        return {
                            title: 'Новая заявка',
                            status: 'secondary'
                        }
                    case 2:
                        return {
                            title: 'Идет обработка данных',
                            status: 'secondary'
                        }
                    case 3:
                        return {
                            title: 'Ошибка отправки в диадок',
                            status: 'danger'
                        }
                    case 4:
                        return {
                            title: 'Отправлен в диадок',
                            status: 'secondary'
                        }
                    case 5:
                        return {
                            title: 'Ошибка отправки в банк',
                            status: 'danger'
                        }
                    case 6:
                        return {
                            title: 'Рассматривается банком',
                            status: 'secondary'
                        }
                    case 7:
                        return {
                            title: 'Отклонен',
                            status: 'danger'
                        }
                    case 8:
                        return {
                            title: 'Дополнительные документы',
                            status: 'secondary'
                        }
                    case 9:
                        return {
                            title: 'Одобрен',
                            status: 'secondary'
                        }
                    case 10:
                        return {
                            title: 'Отклонен пользователем',
                            status: 'danger'
                        }
                    case 11:
                        return {
                            title: 'Резервирование резервного счета',
                            status: 'warning'
                        }
                    case 12:
                        return {
                            title: 'Есть зарезервированный расчетный счет',
                            status: 'secondary'
                        }
                    case 13:
                        return {
                            title: 'Подготовка документов',
                            status: 'warning'
                        }
                    case 14:
                        return {
                            title: 'Подписание',
                            status: 'success'
                        }
                    case 15:
                        return {
                            title: 'Идет обработка данных',
                            status: 'secondary'
                        }
                    case 16:
                        return {
                            title: 'Ошибка отправки в диадок',
                            status: 'danger'
                        }
                    case 17:
                        return {
                            title: 'Отправлен в диадок',
                            status: 'success'
                        }
                    case 18:
                        return {
                            title: 'Ошибка отправки в банк',
                            status: 'danger'
                        }
                    case 19:
                        return {
                            title: 'Проверка документов',
                            status: 'warning'
                        }
                    case 20:
                        return {
                            title: 'Кредит выдан',
                            status: 'success'
                        }
                    default:
                        return {
                            title: '',
                            status: ''
                        }
                }
            },
            selectedBranch(val) {
                this.$router.push('/registration-in-department/1')
            },
            closeConfirmModal() {
                this.rejectOrderConfirm = false
            },
            getCredit(id) {
                // if ( this.company.regbank === 1 ) {
                //     this.$router.push('/signing-documents/1')
                // }
                // else {
                //     this.reservedAccount = true
                // }
                this.signingLoading = true
                this.$store.dispatch('confirmOrder', id).then(() => {
                    this.signingLoading = false
                })
            },
            checkBranches() {
                this.$refs.reservedAccount.closePopup()
                this.selectBranch = true
            },
            nextOrderStep() {
                this.$router.push('/signing-documents/1')
            },
            stepValue(step) {
                return Math.round(step / 10 * 100)
            },
            readCommission(order, read) {
                order[read] = true
                this.requisitesModal = true
            },
            orderReject(id) {
                this.rejectOrderId = id
                this.rejectOrderConfirm = true
            },
            createNewOrder() {
                this.$router.push(`/`)
            },
            visibleStick(status) {
                let bool = true
                if (
                    status === 1  ||
                    status === 8  ||
                    status === 14 ||
                    status === 9
                ) {
                    bool = false
                }
                return bool
            },
            signingOrder (id) {
                this.signingLoading = true
                this.$router.push(`/signing-documents/${id}`).then(() => {
                    this.signingLoading = false
                })
            }
        },
        mounted() {
            this.$store.dispatch('getOrders').then(() => this.loading = false)
            this.$store.dispatch('getOrdersFinished')
        }
    }
</script>
