<template>

    <div class="reject-modals">


        <Popup v-if="confirmModal"
               ref="rejectOrderConfirm"
               @close-modal="closeConfirmModal"
               :classes="'reject-modal-confirm'">

            <div class="popup-title">
                <div class="h3">Вы уверены, что хотите <br>отказаться от кредита?</div>
            </div>

            <div class="reject-confirm-btns">
                <button @click="$refs.rejectOrderConfirm.closePopup()"
                        class="btn btn-lg btn-secondary">Нет</button>
                <button @click="openRejectModal"
                        class="btn btn-lg">Да, уверен</button>
            </div>

        </Popup>

        <Popup v-if="rejectModal"
               ref="rejectModal"
               @close-modal="rejectModal = false"
               :classes="'reject-modal'">

            <div class="popup-title">
                <div class="h2">Отказ от кредита</div>
                <p>Укажите пожалуйста причину отказа</p>
            </div>

            <div class="reject-cause">
                <form class="reject-form"
                      @submit.prevent="rejectSubmit">
                    <v-select v-model="rejectCause"
                              :options="rejectOptions"
                              placeholder="Выбор причины"
                              :searchable="false"
                              :class="{'invalid': rejectModalValidate}"
                              @option:selecting="rejectModalValidateUpdate"/>
                    <button type="submit"
                            class="btn btn-lg btn-block">Готово
                    </button>
                </form>
            </div>

        </Popup>

    </div>

</template>

<script>

    import Popup from "@/components/Blocks/Popup";

    export default {
        name: "RejectOrderModals",
        components: {Popup},
        props: {
            rejectOrderConfirm: {
                type: Boolean,
                default: false
            },
            orderId: [String, Number]
        },
        data: () => ({
            confirmModal: false,
            rejectModal: false,
            rejectCause: '',
            rejectOptions: [
                'Причина 1',
                'Причина 2',
                'Причина 3',
                'Причина 4'
            ],
            rejectModalValidate: false,
        }),
        watch: {
            rejectOrderConfirm(newVal) {
                this.confirmModal = newVal
            }
        },
        methods: {
            openRejectModal() {
                // this.$refs.rejectOrderConfirm.closePopup()
                // this.rejectModal = true
                this.$store.dispatch('rejectOrder', this.orderId).then(() => {
                    this.$refs.rejectOrderConfirm.closePopup()
                })
            },
            rejectSubmit() {
                if ( this.rejectCause.length > 0 ) {
                    this.rejectModalValidate = false
                    this.$refs.rejectModal.closePopup()
                    this.$emit('reject-order-success')
                }
                else {
                    this.rejectModalValidate = true
                }
            },
            rejectModalValidateUpdate(val) {
                if ( val.length > 0 ) {
                    this.rejectModalValidate = false
                }
                else {
                    this.rejectModalValidate = true
                }
            },
            closeConfirmModal() {
                this.confirmModal = false
                this.$emit('close-confirm-modal')
            }
        },
        mounted() {
            this.confirmModal = this.rejectOrderConfirm
        }
    }
</script>

